/*
//------------------------------------------------------------------------------
// Login
//------------------------------------------------------------------------------  
*/
form#user-login {
  padding: 0px 1rem;
}

@media only screen and (min-width: 992px) {
  form#user-login form#user-login {
    padding: 0px 1rem;
  }
}

form#user-login > div {
  background: cornsilk;
  display: flex;
  flex-wrap: wrap;
  background-color: #E6E7E8;
  box-shadow: none;
}

@media only screen and (min-width: 992px) {
  form#user-login > div {
    box-shadow: 15px 15px #F1F1F2;
  }
}

form#user-login .form-type-textfield, form#user-login .form-type-password {
  display: flex;
  justify-content: center;
  flex-direction: column;
  flex: 1 1 100%;
}

form#user-login .form-type-textfield input#edit-name, form#user-login .form-type-password input#edit-pass {
  width: 90%;
  margin: 0 auto;
  background: #ffffff;
  border-radius: 0px;
  height: 46px;
  font-family: Lato Light;
  font-size: 20px;
  transition: none;
  box-shadow: none;
}

@media only screen and (min-width: 992px) {
  form#user-login .form-type-textfield input#edit-name, form#user-login .form-type-password input#edit-pass {
    width: 70%;
  }
}

form#user-login .form-type-textfield input:-webkit-autofill, form#user-login .form-type-password input:-webkit-autofill {
  background-color: #ffffff !important;
}

form#user-login .form-type-textfield label {
  text-align: center;
  font-family: Lato;
  font-size: 48px;
  line-height: 21.89px;
  color: #000000;
  margin: 0px;
  font-weight: normal;
  margin-top: 37px;
  margin-bottom: 54px;
}

form#user-login .form-type-textfield label::after {
  content: "";
  display: inline-block;
  width: 86%;
  border-top: 2px solid #808184;
  margin-top: 8px;
}

form#user-login div#edit-info {
  display: none;
}

@media only screen and (min-width: 992px) {
  form#user-login div#edit-info {
    flex: 2 1 20%;
    font-size: 11px;
    font-family: "Lato Italic";
    margin-left: 45px;
    display: block;
  }
}

form#user-login div#edit-enlace {
  display: flex;
  flex: 0 1 50%;
  justify-content: flex-end;
  padding-right: 16px;
  height: 36px;
  margin: 0px;
  margin-bottom: 22px;
}

form#user-login div#edit-enlace a {
  font-family: Lato;
  font-size: 15px;
  color: #000000;
}

@media only screen and (min-width: 992px) {
  form#user-login div#edit-enlace {
    flex: 0 1 25%;
    justify-content: center;
    align-items: center;
    padding: 0px;
  }
  form#user-login div#edit-enlace a {
    font-family: Lato Light;
    font-size: 15px;
    color: #000000;
  }
}

form#user-login div#edit-actions {
  display: flex;
  flex: 0 1 50%;
  justify-content: flex-start;
  padding-left: 16px;
  margin-bottom: 22px;
}

form#user-login div#edit-actions button#edit-submit {
  background: #41B65B;
  border-color: #41B65B;
  border-radius: 0px;
  font-family: Lato Regular;
  font-size: 15px;
  color: #ffffff;
}

@media only screen and (min-width: 992px) {
  form#user-login div#edit-actions {
    flex: 0 1 21%;
    justify-content: flex-start;
    align-items: center;
    padding: 0px;
  }
}

/*
//------------------------------------------------------------------------------
// Informacion user 
//------------------------------------------------------------------------------ 
*/
.user-agenda {
  background-color: #000000;
  float: left;
  width: 100%;
  padding-top: 27px;
  padding-bottom: 27px;
}

.user-agenda .block-title {
  display: none;
}

.user-agenda .view-user-login {
  display: table;
  padding: 0;
  width: 100%;
}

.user-agenda .view-user-login .view-header {
  border-right: 1px solid #ffffff;
  display: table-cell;
  width: 45%;
}

.user-agenda .view-user-login .view-header .container-user-rol {
  display: block;
}

.user-agenda .view-user-login .view-header .container-user-rol .consejero, .user-agenda .view-user-login .view-header .container-user-rol .estudiante, .user-agenda .view-user-login .view-header .container-user-rol .cordinador {
  width: 50px;
  height: 50px;
  float: left;
  margin: 0px 10px;
}

.user-agenda .view-user-login .view-header .container-user-rol .consejero {
  background: url(../img/consejero.png) no-repeat;
  background-size: contain;
}

.user-agenda .view-user-login .view-header .container-user-rol .estudiante {
  background: url(../img/estudiante.png) no-repeat;
  background-size: contain;
  background-position-x: 13px;
}

.user-agenda .view-user-login .view-header .container-user-rol .cordinador {
  background: url(../img/cordinador.png) no-repeat;
  background-size: contain;
}

.user-agenda .view-user-login .view-header .container-user-rol p {
  margin: 0px;
  padding-top: 6px;
  font-family: "Lato Light";
  font-size: 17px;
  color: #ffffff;
}

.user-agenda .view-user-login .view-header .container-user-rol h2 {
  margin: 0px;
  padding-bottom: 10px;
  font-family: Lato Bold;
  font-weight: bold;
  font-size: 14px;
  line-height: 17.2px;
  color: #ffffff;
}

.user-agenda .view-user-login .view-header .container-user-rol h2::before {
  content: "";
  border-bottom: 5px solid #41B65B;
  width: 62px;
  position: absolute;
  top: 150px;
}

@media only screen and (min-width: 992px) {
  .user-agenda .view-user-login .view-header .container-user-rol h2 {
    font-size: 22px;
  }
}

@media only screen and (min-width: 560px) {
  .user-agenda .view-user-login .view-header {
    width: 25%;
  }
}

.user-agenda .view-user-login .view-content {
  display: table-cell;
  width: 50%;
}

.user-agenda .view-user-login .view-content .item-list {
  padding: 0px;
}

.user-agenda .view-user-login .view-content .item-list ul {
  list-style: none;
  margin: 0px;
  -webkit-margin-before: 0em;
  -webkit-margin-after: 0em;
  -webkit-padding-start: 10px;
  -moz-padding-start: 10px;
}

.user-agenda .view-user-login .view-content .item-list ul span {
  color: #ffffff;
  font-family: "Lato Bold";
  font-size: 0.7rem;
}

@media only screen and (min-width: 560px) {
  .user-agenda .view-user-login .view-content .item-list ul span {
    font-size: 1rem;
  }
}

.user-agenda .view-user-login .view-content .item-list ul .views-field-field-carrera {
  display: inline-flex;
  color: #ffffff;
  font-family: "Lato Bold";
  font-size: 0.7rem;
}

.user-agenda .view-user-login .view-content .item-list ul .views-field-field-carrera .field-content {
  padding-left: 5px;
}

@media only screen and (min-width: 560px) {
  .user-agenda .view-user-login .view-content .item-list ul .views-field-field-carrera .field-content {
    font-size: 1rem;
  }
}

@media only screen and (min-width: 560px) {
  .user-agenda .view-user-login .view-content .item-list ul   {
    -webkit-padding-start: 30px;
    -moz-padding-start: 30px;
  }
}

@media only screen and (min-width: 992px) {
  .user-agenda .view-user-login .view-content {
    width: 75%;
  }
}

/*
//------------------------------------------------------------------------------
// Nvegacion Agenda
//------------------------------------------------------------------------------
*/
